.questions {
	padding-top: 25px;
	padding-bottom: 40px; }

.questions-item {
	padding: 32px 0;
	&__inner {
		display: flex; }
	&:nth-child(even) {
		background-color: $powder; }
	&__left {
		@include flex-size(25%);
		color: $green;
		font-weight: 700; }
	&__right {
		padding-left: 70px;
		font-size: 12px;
		color: $black;
		p {
			&:not(:last-child) {
				padding-bottom: 10px; } } } }
