.delivery-info {
	text-align: center;
	padding: 35px 0 32px;
	color: $brown;
	@include md {
		padding: 10px 0 36px; }
	&__inner {
		max-width: 1000px; }
	&__title.title {
		color: $green; }
	&__wrap {
		margin: 15px -45px 0;
		display: flex;
		justify-content: center;
		@include lg {
			margin: 25px -25px 0; }
		@include md {
			flex-wrap: wrap;
			margin: 25px 0 0; }
		@include sm {
			padding: 0 30px; } }
	&__item {
		padding: 0 35px;
		width: 33.3%;
		@include lg {
			padding: 0 25px; }
		@include md {
			width: 100%;
			max-width: none;
			padding: 0;
			&:not(:first-child) {
				margin-top: 20px; } } }
	&__btn {
		text-align: center;
		margin-top: 30px;
		@include md {
			.btn {
				background-color: $pink; } } } }


.delivery-item {
	text-align: center;
	width: 100%;
	@include md {
		display: flex;
		justify-content: center;
		align-items: center; }
	@include ms {
		&.delivery-item_sm {
			justify-content: space-between; } }
	&__img {
		min-height: 165px;
		display: flex;
		justify-content: center;
		align-items: flex-end;
		@include md {
			min-height: auto; }
		@include sm {
			max-width: 166px;
			.delivery-item_sm & {
				max-width: 87px;
				margin-left: 35px; } }
		@include ms {
			.delivery-item_sm & {
				margin-left: 20px; } }
		img {
			display: block;
			max-width: 100%; } }
	&__text {
		margin-top: 27px;
		@include f(16px, 700);
		@include circe;
		line-height: 1.2;
		span {
			text-transform: uppercase; }
		@include md {
			margin-left: 20px;
			margin-top: 0;
			max-width: 300px; }
		@include sm {
			max-width: 200px;
			.delivery-item_sm & {
				margin-left: 50px; } } } }
