.form {
	&__row {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin: 0 -15px; }
	&__col {
		padding: 0 15px;
		@include flex-size(40%);
		@include sm {
			@include flex-size(100%); }
		&-md {
			padding: 0 15px;
			@include flex-size(60%);
			@include sm {
				@include flex-size(100%); } } }
	&__contacts-input {
		max-width: 775px;
		width: 100%; }
	&__contacts-text {
		padding-top: 25px; }
	&__contacts-textarea {
		padding-top: 30px; }
	&__select {
		padding-top: 15px;
		user-select: none;
		position: relative;
		.btn {
			@include roboto;
			font-size: 15px;
			font-weight: 400;
			padding: 15px;
			min-width: 370px;
			background-color: $white;
			text-transform: none;
			border-radius: 5px;
			&:focus {
				outline: none!important; }
			&:before {
				display: none; } }
		.dropdown {
			min-width: 370px;
			&-toggle {
				&:after {
					content: '';
					position: absolute;
					margin: -4px 0 0 0;
					vertical-align: top;
					border: none;
					background-image: url(../img/arrow-down.svg);
					@include cover;
					width: 16px;
					height: 8px;
					top: 50%;
					right: 15px; } } }
		.bootstrap-select .dropdown-toggle .filter-option {
			position: relative;
			padding: 0; }
		.bootstrap-select>.dropdown-toggle.bs-placeholder {
			color: $black; }
		.filter-option {
			&-inner-inner {
				overflow: auto; } }
		.dropdown-menu {
			margin: 0;
			padding: 0;
			background-color: $white;
			border-radius: 0 0 5px 5px;
			border: none;
			&.inner {
				background-color: $white; }
			&[x-placement="top-start"] {
				border-radius: 5px 5px 0 0; } }
		.inner {
			padding: 0; }
		.selected {
			display: none; }
		.dropdown-item {
			@include roboto;
			font-size: 15px;
			color: $black;
			padding: 15px;
			text-transform: none;
			transition: background-color .3s;
			background-color: transparent;
			@include hover {
				background-color: rgba(#fbf2e6, .5); } } }
	&__btn {
		margin-top: 10px;
		.thanks & {
			text-align: left; }
		.subscription & {
			margin-top: 0;
			margin-left: 65px;
			@include md {
				margin-left: 20px; } }
		.form_main & {
			margin-top: 35px; }
		.person & {
			margin-top: 40px;
			text-align: left; }
		.enter & {
			margin-top: 32px;
			text-align: left; } }
	.subscription & {
		display: flex;
		align-items: center; }
	.review-form & {
		padding-top: 20px; } }

.form-group {
	position: relative;
	margin-top: 10px;
	.subscription & {
		max-width: 427px;
		width: 100%;
		margin-top: 0; }
	input, textarea {
		width: 100%;
		@include roboto;
		@include f(15px, 400);
		color: $brown;
		padding: 13px 12px;
		border-radius: 5px;
		@include placeholder {
			@include roboto;
			@include f(15px, 400);
			color: $brown;
			@include lg {
				font-size: 14px; }
			@include sm {
				font-size: 12px; }
			.contacts__form & {
				color: rgba($black, .5); }
			.rating__form & {
				color: rgba($black, .5); } }
		@include lg {
			font-size: 14px;
			padding: 10px; }
		@include sm {
			font-size: 12px; }
		&:focus {
			@include placeholder {
				font-size: 0; } } }
	textarea {
		resize: none;
		min-height: 100px;
		height: 100%;
		border: none;
		.contacts__form & {
			min-height: 137px; }
		.rating__form & {
			min-height: 156px;
			padding: 12px 18px; }
		.review-form & {
			min-height: 145px; } }

	.form_main & {
		input, textarea {
			color: $black;
			@include placeholder {
				color: $black; } } } }



.help-block.form-error {
	font-size: 15px;
	color: $red; }
