.title {
	@include circe;
	@include f(30px, 700, 1.2);
	text-transform: uppercase;
	color: $brown;
	@include md {
		font-size: 26px; }
	@include sm {
		font-size: 21px; }
	&_lg {
		font-size: 30px;
		letter-spacing: -0.015em;
		@include md {
			font-size: 30px; }
		@include sm {
			font-size: 26px; } }
	&_m {
		font-size: 23px;
		line-height: 1.5;
		letter-spacing: -0.02em;
		@include lg {
			font-size: 21px; }
		@include sm {
			font-size: 16px; }
		span {
			color: $green; } }
	&_s {
		@include f(22px, 700, 1);
		color: $white;
		@include lg {
			font-size: 18px; } } }

.title-s {
	@include circe;
	@include f(22px, 700);
	text-transform: uppercase;
	&:not([class*="text"]) {
		.modal & {
			color: $brown; } }
	@include sm {
		font-size: 18px; } }

.title-xs {
	@include circe;
	@include f(20px, 700);
	text-transform: uppercase;
	@include sm {
		font-size: 16px; } }

.title-rob-xs {
	@include roboto;
	@include f(18px, 700);
	text-transform: uppercase; }
.title-rob-xxs {
	@include roboto;
	@include f(16px, 700);
	text-transform: uppercase; }


.description {
	@include roboto;
	@include f(20px, 400);
	color: $black;
	@include lg {
		font-size: 14px; } }

.article,
article {
	p {
		line-height: 1.4;
		&:not(:first-child) {
			margin-top: 10px; }
		&:not(:last-child) {
			margin-bottom: 10px; } }
	ol {
		font-size: 12px;
		line-height: 1.68;
		counter-reset: num;
		list-style-type: none;
		padding-left: 30px;
		li {
			padding-left: 25px;
			position: relative;
			&:before {
				counter-increment: num;
				content: counter(num);
				position: absolute;
				left: 0;
				top: 0;
				// height: 1px
 } } }				// width: 15px
	ul {
		font-size: 12px;
		line-height: 1.68;
		li {} }
	&__title,
	h1 {
		@include circe;
		@include f(30px, 700, 1.2);
		text-transform: uppercase;
		color: $brown;
		@include md {
			font-size: 26px; }
		&_lg {
			font-size: 30px;
			letter-spacing: -0.015em;
			@include md {
				font-size: 30px; } }
		&_m {
			font-size: 23px;
			line-height: 1.5;
			letter-spacing: -0.02em;
			@include lg {
				font-size: 21px; }
			@include sm {
				font-size: 16px; }
			span {
				color: $green; } }
		&_s {
			@include f(22px, 700, 1);
			color: $white;
			@include lg {
				font-size: 18px; } }
		&:not(:last-child) {
			margin-bottom: 52px;
			margin-top: 50px; } }
	h2, h3, h4 {
		@extend .title-s;
		color: $brown;
		&:not(:last-child) {
			margin-bottom: 20px;
			margin-top: 40px; } }
	h3 {
		font-size: 18px; }
	h4 {
		font-size: 16px; } }
