.btn {
	padding: 9px 21px;
	@include roboto;
	color: $white;
	@include f(14px,500,1);
	text-transform: uppercase;
	text-align: center;
	text-decoration: none;
	vertical-align: top;
	display: inline-block;
	border-radius: 20.5px;
	background-color: $orange;
	position: relative;
	overflow: hidden;
	transition: background-color .3s, color .3s;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: $black;
		opacity: 0;
		transition: opacity .3s; }
	span {
		position: relative;
		z-index: 2; }
	@include hover {
		&:before {
			opacity: 0.2; } }
	&_sm {
		min-width: 112px; }
	&_md {
		min-width: 180px;
		@include lg {
			min-width: 166px; } }
	&_l {
		min-width: 150px; }
	&_lg {
		min-width: 240px; }
	&_wide {
		width: 100%; }
	&_blue {
		background-color: $blue; }
	&_pink {
		background-color: $pink; }
	&_green {
		background-color: $green; }
	&_xl {
		font-size: 20px;
		padding: 16px 52px;
		border-radius: 26px;
		@include lg {
			font-size: 16px;
			padding: 16px 52px; }
		@include md {
			font-size: 14px;
			min-width: 270px;
			padding: 15px 25px; }
		@include ms {
			min-width: auto; } }
	&_plus {
		padding-left: 57px;
		@include sm {
			padding-left: 43px; }
		svg {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 15px;
			font-size: 1.5em;
			@include sm {
				font-size: 1.2em; } } }
	&_square {
		padding: 10px 21px;
		border-radius: 0;
		@include circe;
		font-size: 30px;
		font-weight: 400;
		text-transform: none;
		min-width: 333px; }
	&_bonus {
		font-size: 20px;
		padding: 15px 21px;
		border-radius: 50px;
		@include md {
			font-size: 16px; } } }
.btn-close {
	display: inline-block;
	width: 32px;
	height: 32px;
	border-radius: 50%;
	border: 1px solid $white;
	background-color: transparent;
	outline: none;
	position: relative;
	@include sm {
		width: 25px;
		height: 25px; }
	&:before, &:after {
		content: '';
		position: absolute;
		width: 20px;
		height: 2px;
		background-color: $white;
		color: $white;
		top: 50%;
		left: 50%;
		@include sm {
			width: 16px; } }
	&:before {
		transform: translate(-50%, -50%) rotate(45deg); }
	&:after {
		transform: translate(-50%, -50%) rotate(-45deg); }
	&_brown {
		border-color: $brown;
		&:before, &:after {
			background-color: $brown; } } }

.btn-border {
	padding: 8px 28px 8px 55px;
	@include roboto;
	color: $brown;
	@include f(14px,500,1);
	letter-spacing: 0.005em;
	text-transform: uppercase;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	border-radius: 30px;
	background-color: transparent;
	position: relative;
	overflow: hidden;
	border: 1px solid $brown;
	transition: background-color .3s, color .3s;
	svg {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 15px;
		font-size: 1.1em; } }

.btn-plus {
	padding: 0;
	font-size: 25px;
	@include circle(46);
	svg {
		position: absolute;
		@include vcenter; } }

.btn-top {
	opacity: 0;
	visibility: hidden;
	@include circle(60);
	background-color: transparent;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 1px solid $brown;
	position: fixed;
	right: 40px;
	bottom: 70px;
	z-index: 5;
	color: $brown;
	font-size: 40px;
	transition: opacity .3s, visibility .3s;
	&.is-active {
		opacity: 1;
		visibility: visible; }
	.icon {
		transform: rotate(-90deg); } }
