.delivery {
	padding-top: 50px;
	padding-bottom: 40px;
	&__top {
		display: flex;
		margin: 0 -45px; }
	&__option {
		width: 50%;
		padding: 0 45px;
		&-title {
			padding-bottom: 36px; }
		p {
			&:not(:last-child) {
				padding-bottom: 10px; } } }
	&__map {
		@include cover;
		width: 100%;
		height: 0;
		padding-bottom: 46.8%;
		&-wrap {
			padding-top: 30px; } }
	&__day {
		padding-top: 50px;
		display: flex;
		&-left {
			width: 35%;
			padding-right: 40px; }
		&-text {
			padding-top: 10px; }
		&-right {
			display: flex; }
		&-check {
			padding-top: 67px;
			&:not(:first-child) {
				padding-left: 55px; } } }
	&__interval {
		padding-top: 40px;
		display: flex; }
	&__pay {
		padding-top: 60px;
		&-title {}
		&-content {
			padding-top: 14px; }
		&-items {
			display: flex;
			justify-content: space-between; }
		&-aside {
			padding-top: 30px;
			a {
				text-decoration: underline; } } } }

.interval {
	display: flex;
	&__item {
		position: relative;
		&:not(:first-child) {
			padding-left: 137px;
			&:before {
				content: '';
				position: absolute;
				top: 24px;
				left: 0;
				width: 100%;
				height: 2px;
				background-color: $green; } } }
	.delivery__day-right & {
		padding-top: 10px; }
	&__circle {
		@include circle(50);
		background-color: $powder;
		position: relative;
		&:after {
			content: '';
			@include vcenter;
			@include circle(35);
			background-color: $green; } }
	&__time {
		text-align: center;
		padding-top: 13px;
		font-size: 18px;
		font-weight: 700; } }

.pay-item {
	display: flex;
	align-items: center;
	.delivery__pay-items & {
		padding-right: 40px; }
	&__icon {
		@include circle(85);
		background-color: $green;
		@include cover;
		background-size: 70%;
		margin-right: 25px;
		flex-shrink: 0;
		.pay-item_icon-sm & {
			background-size: 55%; } } }
