.footer {
	padding-top: 33px;
	background-color: rgba($brown, .1);
	@include sm {
		padding-top: 6px; }
	&__inner {}
	&__top {
		display: flex;
		justify-content: space-between;
		padding-bottom: 36px;
		color: $brown;
		flex-wrap: wrap;
		@include lg {
			padding-bottom: 13px; } }
	&__logo {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		@include md {
			display: none; } }
	&-logo {
		font-size: 147px;
		.icon {
			display: block;
			max-width: 100%;
			color: $brown; } }
	&-slogan {
		margin-top: 20px;
		text-align: center;
		font-weight: 700;
		@include circe;
		@include lg {
			font-size: 16px; } }
	&__menu {
		display: flex;
		@include sm {
			display: block;
			column-count: 2;
			column-gap: 68px;
			width: 100%; }
		@include ms {
			column-gap: 20px; }
		&-wrap {
			text-transform: uppercase;
			padding-top: 30px;
			margin: 0 20px;
			display: flex;
			@include md {
				width: 100%;
				margin: 0; }
			@include sm {
				display: block;
				margin: 0; } }
		&-title {
			font-weight: 700; }
		&-block {
			padding: 0 35px;
			margin: -8px 0;
			@include lg {
				padding: 0 15px; }
			@include sm {
				margin: 0;
				padding: 0; } }
		&-item {
			padding: 8px 0;
			transition: color .3s;
			@include hover {
				color: $green; }
			@include sm {
				padding: 5px 0; } } }
	&__contacts {
		text-transform: uppercase;
		padding-top: 30px;
		flex-shrink: 0;
		color: $brown;
		@include md {
			display: flex;
			font-size: 18px;
			padding-top: 20px; }
		@include ms {
			flex-wrap: wrap; }
		&-item {
			@include ms {
				width: 100%; }
			&:not(:first-child) {
				margin-top: 50px;
				@include md {
					margin-top: 0;
					margin-left: 50px; }
				@include ms {
					margin-left: 0;
					margin-top: 20px; } }
			span {
				font-size: 24px;
				@include lg {
					font-size: 20px; }
				@include md {
					font-size: 25px; } } } }
	&__bottom {
		padding: 15px 0 20px;
		background-color: $brown;
		@include md {
			padding: 40px 0; }
		&-wrap {
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex-wrap: wrap;
			@include sm {
				justify-content: center; } }
		&_big {
			padding-bottom: 17px; } }
	&__copyright {
		color: $white;
		font-size: 10px;
		@include md {
			display: none; } }
	&__social {
		display: flex;
		align-items: center;
		margin: 0 15px;
		@include sm {
			flex-wrap: wrap;
			margin: 0;
			justify-content: center; } }
	// &__stores
	// 	+r(864)
	// 		margin-top: 35px
	// 	+sm
	// 		margin-top: 0
	&__agree {
		width: 100%;
		margin-left: 240px;
		margin-top: -20px;
		@include md {
			margin-top: 23px;
			margin-left: 0; }
		&-link {
			display: inline-block;
			text-decoration: underline;
			color: $brown;
			font-size: 11px; } }
	&-wrap {
		margin-top: 40px; } }

.subscribe {
	display: block;
	color: $white;
	text-decoration: none;
	text-transform: uppercase;
	transition: color .3s;
	font-size: 13px;
	@include hover {
		color: $orange; }
	@include lg {
		font-size: 17px; }
	@include md {
		font-size: 18px; }
	@include r(497) {
		width: 100%;
		margin-top: 30px; }
	&:not(:first-child) {
		margin-left: 41px;
		@include lg {
			margin-left: 20px; }
		@include r(497) {
			margin-left: 0;
			text-align: center; } }
	.header__social & {
		@include f(12px, 500);
		margin-top: 3px;
		letter-spacing: 0.62px;
		@include lg {
			font-size: 12px;
			margin-right: 10px; } } }

.social {
	display: flex;
	align-items: center;
	&__item {
		font-size: 27px;
		color: $white;
		transition: color .3s;
		@include hover {
			color: $orange; }
		@include md {
			font-size: 40px; }
		&:not(:first-child) {
			margin-left: 24px; }
		svg {
			display: block;
			max-width: 100%; }
		.header__social & {
			font-size: 20px;
			flex-shrink: 0;
			&:not(:first-child) {
				margin-left: 19px;
				@include lg {
					margin-left: 10px; } }
			@include lg {
				font-size: 18px; } } } }

.stores {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-right: 4px;
	&__item {
		max-width: 110px;
		@include md {
			max-width: 160px;
			width: 100%; }
		a {
			display: block; }
		&:not(:first-child) {
			margin-left: 35px; }
		img {
			display: block;
			max-width: 100%;
			width: 100%; } } }
