.header {
	@include md {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 10;
		width: 100%; }
	&__top {
		background-color: $green;
		color: $white;
		@include md {
			padding: 12px 0 14px; } }
	&__inner {
		position: relative;
		display: flex;
		@include md {
			justify-content: space-between;
			align-items: center; } }
	&__left,
	&__right {
		width: 50%;
		display: flex;
		@include md {
			width: auto; } }
	&__left {
		padding-right: 80px;
		@include md {
			display: none; } }
	&__right {
		justify-content: flex-end;
		padding-left: 80px;
		@include md {
			padding: 0;
			// justify-content: center
			width: 100%; } }
	&__title {
		display: none;
		@include md {
			display: block;
			font-size: 43px;
			a {
				display: flex; } } }
	&__logo {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		background-color: $white;
		width: 150px;
		padding: 10px 22px;
		z-index: 1;
		img {
			display: block;
			max-width: 100%; }
		a {
			display: block; }
		@include md {
			display: none; } }
	&__phone {
		margin-left: 8px;
		display: flex;
		align-items: center;
		font-weight: 500;
		span {
			font-size: 22px;
			letter-spacing: 0.04em; } }
	&__city {
		height: 100%; }
	&__bottom {
		padding: 49px 0 16px;
		color: $green;
		@include md {
			padding: 15px 0;
			background-color: $white; } }
	&__social {
		font-size: 12px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 35px 0 25px;
		@include md {
			display: none; } } }

.main-menu {
	display: flex;
	align-items: center;
	letter-spacing: 0.05em;
	font-weight: 500;
	text-transform: uppercase;
	&__item {
		position: relative;
		padding: 27px 0 10px;
		display: flex;
		align-items: center;
		line-height: 1;
		white-space: nowrap;
		transition: color .3s;
		@include hover {
			color: $orange; }
		&:not(:first-child) {
			margin-left: 35px;
			@include lg {
				margin-left: 20px; } }
		&:hover {
			.main-menu__submenu {
				opacity: 1;
				visibility: visible; } }
		&_submenu {
			z-index: 1; } }
	&__link {
		display: flex;
		align-items: center; }
	&__arrow {
		display: block;
		font-size: 7px;
		margin-left: 15px;
		.icon {
			display: block; } }
	&__submenu {
		position: absolute;
		top: 0;
		left: -21px;
		padding: 70px 21px 30px;
		background-color: rgba($lightGray, .8);
		opacity: 0;
		visibility: hidden;
		z-index: -1;
		font-weight: 700;
		letter-spacing: 0.025em;
		color: $brown;
		transition: opacity .3s, visibility .3s;
		&-item {
			transition: color .3s;
			@include hover {
				color: $green; }
			&:not(:first-child) {
				margin-top: 25px; } }
		&-link {
			display: block;
			white-space: nowrap; } } }

.basket {
	display: flex;
	align-items: center;
	&__text {
		display: flex;
		align-items: baseline;
		font-weight: 500;
		line-height: .9;
		font-size: 16px;
		letter-spacing: 0.054em;
		@include md {
			font-size: 15px; }
		@include sm {
			margin: 10px 10px 10px 0;
			font-size: 12px; }
		// +ms
		// 	width: 100%
		// 	padding-bottom: 20px
		// 	justify-content: center
		.choice__basket & {
			font-size: 15px; } }
	&__title {
		text-transform: uppercase;
		@include sm {
			font-size: 10px; } }
	&__sum {
		margin-left: 10px;
		font-size: 30px;
		font-weight: 700;
		@include sm {
			margin-left: 5px;
			font-size: 14px; }
		.card__basket & {
			font-size: 25px;
			margin: 0; }
		.choice__basket & {
			font-size: 37px;
			@include md {
				font-size: 24px; } } }
	&__currency {
		margin-left: 2px;
		font-weight: 500;
		.card__basket & {
			font-size: 18px; }
		.choice__basket & {
			font-size: 20px; } }
	&__weight {
		margin-left: 40px;
		font-size: 20px;
		color: $black; }
	&__btn {
		margin-left: 12px;
		@include md {
			margin-left: 40px; }
		@include ms {
			margin-left: 0; }
		.choice__basket & {
			margin-left: 27px;
			@include md {
				margin: 0 20px; }
			.btn_sm {
				padding: 16px 45px;
				font-size: 20px;
				border-radius: 34px;
				font-weight: 500;
				line-height: 1;
				text-transform: uppercase;
				letter-spacing: 0.002em;
				@include md {
					font-size: 12px; } } } }
	@include ms {
		flex-wrap: wrap;
		justify-content: center;
		width: 100%; }
	&.card__basket {
		color: $brown; } }

.city {
	position: relative;
	display: flex;
	align-items: center;
	z-index: 1;
	padding-right: 18px;
	height: 100%;
	&:hover {
		.city__list {
			opacity: 1;
			visibility: visible; }
		.city__current, .city__item, .city__arrow {
			color: $brown; } }
	&__current {
		z-index: 1;
		letter-spacing: 0.06em;
		transition: color .3s; }
	&__arrow {
		font-size: 7px;
		margin-left: 26px;
		margin-top: 6px;
		z-index: 1;
		transition: color .3s;
		.icon {
			display: block; } }
	&__list {
		position: absolute;
		top: 0;
		left: -23px;
		right: 0;
		padding: 60px 23px 20px;
		background-color: rgba($lightGray,.8);
		opacity: 0;
		visibility: hidden;
		transition: opacity .3s, visibility .3s; }
	&__item {
		transition: color .3s;
		@include hover {
			color: $orange; }
		a {
			display: block; } } }
.account {
	padding: 0 15px;
	display: flex;
	align-items: center;
	@include md {
		flex-wrap: wrap;
		justify-content: center; }
	&-wrap {
		padding: 19px 0;
		@include lg {
			padding: 15px 0; }
		&_bg {
			background-color: $pink; }
		@include md {
			display: none; }
		.mob-menu & {
			display: flex;
			justify-content: center;
			align-items: center; } }
	&__icon {
		font-size: 30px;
		margin-right: 10px;
		@include md {
			margin: 0;
			padding: 0 20px; }
		.icon {
			display: block; } }
	&__text {
		width: 310px;
		padding-right: 20px;
		@include lg {
			width: 250px; }
		@include md {
			width: 100%;
			text-align: center;
			padding: 10px 0; } }
	&__points {
		display: flex;
		flex-direction: column;
		align-items: center;
		line-height: 1;
		&-number {
			font-size: 24px;
			font-weight: 700; }
