.header__toggle {
	cursor: pointer;
	display: none;
	align-items: center;
	width: 31px;
	height: 30px;
	@include md {
		display: flex; } }

.header__toggle-icon {
	position: relative;
	width: 100%;
	height: 4px;
	background-color: $white;
	&:before, &:after {
		content: '';
		position: absolute;
		width: 100%;
		height: 4px;
		left: 0;
		background-color: $white;
		transition: transform .3s; }
	&:before {
		top: -12px; }
	&:after {
		bottom: -12px; }
	&.is-open {
		background-color: transparent;
		&:before {
			@include rotate(45);
			top: 0;
			background-color: $white; }
		&:after {
			@include rotate(-45);
			bottom: 0;
			background-color: $white; } } }

.mob-menu {
	visibility: hidden;
	opacity: 0;
	position: fixed;
	top: 0;
	left: 0;
	max-width: 315px;
	width: 100%;
	background-color: rgba($brown, .9);
	color: $white;
	font-size: 20px;
	padding: 15px 0;
	z-index: 15;
	transition: visibility .3s, opacity .3s;
	@include ms {
		top: 67px; }
	&__list {
		text-transform: uppercase;
		border-bottom: 1px solid $mob-border;
		padding-bottom: 30px; }
	&__item {
		position: relative;
		&.is-active {
			background-color: $brown;
			&:before {
				content: '';
				position: absolute;
				width: 9px;
				height: 100%;
				top: 0;
				left: 0;
				background-color: $green;
				color: $green; } } }
	&__link {
		display: block;
		padding: 14px 0 14px 40px; }
	&__submenu {
		padding-left: 40px;
		&-link {
			display: block;
			position: relative;
			padding: 12px 0 12px 20px;
			&.is-active {
				background-color: $brown;
				&:before {
					content: '';
					position: absolute;
					width: 9px;
					height: 100%;
					top: 0;
					left: 0;
					background-color: $green;
					color: $green; } } } }
	&__contacts {
		padding: 25px 0 25px 40px;
		border-bottom: 1px solid $mob-border;
		&-wrap {
			display: flex;
			align-items: center; } }
	&__arr {
		width: 24px;
		height: 24px;
		@include cover;
		margin-right: 22px; }
	&__city {
		font-size: 22px; }
	&__phone {
		font-size: 18px;
		margin-left: 44px;
		font-weight: 500;
		& span {
			font-size: 30px; } }
	&__buttons {
		padding: 25px 0 25px 40px;
		border-bottom: 1px solid $mob-border;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		& .btn {
			padding: 12px 35px;
			font-size: 16px;
			&:not(:first-child) {
				margin-top: 25px; } } }
	&__socials {
		color: $white;
		font-size: 26px;
		line-height: 1;
		padding: 25px 0 20px 40px;
		display: flex;
		align-items: center;
		border-bottom: 1px solid $mob-border;
		&-icon {
			display: block;
			transition: color .3s;
			&:not(:first-child) {
				margin-left: 20px; }
			@include hover {
				color: $orange; } }
		.icon-vk-m {
			font-size: 34px; } }
	&__aside {
		padding: 25px 0 25px 40px;
		font-size: 20px;
		text-transform: uppercase;
		a {
			display: block;
			font-size: 20px;
			text-transform: uppercase;
			padding: 10px 0; } }
	@include ms {
		max-width: none; }
	&.is-open {
		@include md {
			visibility: visible;
			opacity: 1;
			overflow-y: scroll;
			height: 100vh; } } }
