.info {
	padding: 25px 0;
	&__inner {
		@include md {
			padding: 0; } } }


.info-item {
	display: flex;
	align-items: center;
	position: relative;
	// .about &
	// 	min-height: 350px
	&:before {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: $orange-bg;
		top: 0;
		left: 50%;
		z-index: -1;
		@include md {
			left: 30%; } }
	&:not(:last-child) {
		margin-bottom: 20px; }
	&__img {
		// max-width: 1065px
		// width: 66.5%
		@include flex-size(67%);
		height: 0;
		padding-bottom: 31%;
		@include cover;
		.info-item_order & {
			order: 2; }
		.about & {
			padding-bottom: 29.5%; }
		@include md {
			min-height: 276px; }
		@include sm {
			@include flex-size(50%); } }
	&__main {
		margin: 0 auto;
		padding: 0 35px;
		@include ms {
			padding: 0 10px; }
		.about-item & {
			max-width: 250px; }
		.vacancies__photo & {
			padding: 0 0 0 85px;
			@include lg {
				padding-left: 40px; } } }
	&__title {
		@include sm {
			font-size: 22px; }
		@include ms {
			font-size: 18px; } }
	&__btn {
		margin-top: 30px;
		// +lg
		// 	margin-top: 20px
		@include ms {
			.btn_md {
				min-width: auto; } } }
	&.info-item_order {
		&:before {
			left: -50%;
			@include md;
			left: -30%; } } }
