.stock-page {
	padding-top: 46px;
	@include sm {
		padding-top: 20px; }
	&__pagination {
		display: flex;
		justify-content: flex-end;
		padding-bottom: 24px;
		padding-right: 6px; }
	&__item {
		margin-bottom: 40px; } }

.offer {
	display: flex;
	@include sm {
		flex-direction: column; }
	&__img {
		flex-shrink: 0;
		width: 39%;
		min-height: 355px;
		@include sm {
			flex-shrink: 1;
			min-height: 0;
			width: 100%;
			font-size: 0; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center; }
		.blog & {
			min-height: 484px;
			width: 50%;
			@include sm {
				min-height: 0;
				width: 100%; } } }
	&__content {
		position: relative;
		padding: 40px 0 30px 50px;
		background-color: $orange-bg;
		@include sm {
			padding: 20px; }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 100%;
			width: 200%;
			height: 100%;
			background-color: $orange-bg;
			@include sm {
				display: none; } } }
	&__link {
		display: block;
		color: $green;
		text-decoration: underline;
		font-size: 20px;
		font-weight: 700;
		@include circe;
		margin-bottom: 30px;
		@include sm {
			margin-bottom: 10px; } }
	&__date {
		color: $pink;
		padding-bottom: 8px;
		text-transform: uppercase;
		.blog & {
			color: $brown;
			font-size: 20px;
			padding-bottom: 25px;
			@include sm {
				padding-bottom: 8px; } } }
	&__title {
		color: $brown;
		line-height: 1.2;
		padding-bottom: 15px;
		text-transform: uppercase;
		@include sm {
			padding-bottom: 0; }
		.blog & {
			font-size: 20px;
			padding-bottom: 11px; }
		br {
			@include sm {
				display: none; } } }
	p {
		font-size: 12px;
		line-height: 1.2;
		&:not(:first-child) {
			padding-top: 10px; } }
	&__btn {
		padding-top: 37px;
		@include sm {
			padding-top: 18px; } } }
